import './Dashboard.scss'
import React, { useState, useRef, useEffect } from 'react'
import { Outlet, NavLink, useNavigate } from "react-router-dom";
import { Stack, IconButton, Typography, Tooltip, useMediaQuery } from '@mui/material'
import MenuIcon from '@mui/icons-material/Menu';
import leaf from '../../assets/images/leaf.svg';
import poll from '../../assets/images/poll.svg';
import accountMultiple from '../../assets/images/account-multiple.svg';
import creditCardOutline from '../../assets/images/credit-card-outline.svg';
import setting from '../../assets/images/setting.svg';
import powerPlug from '../../assets/images/power-plug.svg';
import Header from '../../components/header/Header';
import { appapi } from '../../api/apiClient';
import { useDispatch } from 'react-redux';
import { setCities, setCountries, setRoles } from '../../redux/features/appSlice';
import { login } from '../../redux/features/authSlice';
import { useSelector } from 'react-redux';
import { colors } from '../../utils/colors';
import useOutsideClick from '../../components/DetectOutsideClick';
import { routingList } from '../../utils/commonUtils';
import { setLoading } from '../../redux/features/appSlice';
import { updateSideMenuList } from '../../redux/features/authSlice';

const menuArr = [
    {
        name: 'Eco Initiatives',
        flag: false,
        icon: leaf,
        route: 'ecoInitiatives',
        moduleName: 'eco_initiatives'
    },
    {
        name: 'Analytics',
        flag: false,
        icon: poll,
        route: 'analytics',
        moduleName: 'reporting_analytics'
    },
    {
        name: 'Payments',
        flag: false,
        icon: creditCardOutline,
        route: 'payments',
        moduleName: 'payments'
    },
    {
        name: 'User Management',
        flag: false,
        icon: accountMultiple,
        route: 'userManagement',
        moduleName: 'user_management'
    },
    {
        name: 'Integrations',
        flag: false,
        icon: powerPlug,
        route: 'integrations',
        moduleName: 'integrations'
    },
    {
        name: 'Settings',
        flag: true,
        icon: setting,
        route: 'settings',
        moduleName: 'settings'
    }
]

export default function Dashboard() {

    const { user } = useSelector((state) => state.auth)

    const [sideMenuList, setSideMenuList] = useState(menuArr)

    const navigate = useNavigate()

    useEffect(() => {
        let arr = [...menuArr];
        let permissions = user?.permission;
        arr.map((menu) => {
            let find = permissions?.find((permission) => permission.moduleName === menu.moduleName)
            if (find) {
                if (find.viewAccess === '0' && find.editAccess === '0' && find.deleteAccess === '0' && find.activateAccess === '0') {
                    if (menu.moduleName === find.moduleName) {
                        menu.flag = false
                    }
                } else {
                    menu.flag = true
                }
            }
        })
        setSideMenuList(arr)
    }, [user]);

    const dispatch = useDispatch()

    useEffect(() => {
        let arr = JSON.parse(JSON.stringify(sideMenuList));
        dispatch(updateSideMenuList([...arr]))
    }, [sideMenuList]);



    //check whether mobile or not
    const isMobile = useMediaQuery('(max-width:1000px)');

    //Check OutsideClick to close the nav
    const boxRef = useRef();
    useOutsideClick(boxRef, () => isMobile && setExpanded(false));

    const [expanded, setExpanded] = useState(false);

    /**
    * retrieving Cities List 
    */
    const getcities = async () => {
        const response = await appapi.get(`/city`);
        if (response.ok) {
            const data = response.data;
            if (data.status === 'success') {
                let cities = data?.data;
                dispatch(setCities(cities))
            }
        }
    }

    /**
    * retrieving Country List 
    */
    const getcountries = async () => {
        const response = await appapi.get(`/country`);
        if (response.ok) {
            const data = response.data;
            if (data.status === 'success') {
                dispatch(setCountries(data?.data))
            }
        }
    }

    /**
     * retrieving Roles List 
     */
    const getRoles = async () => {
        let params = {
            params: {
                enterpriseId: user.enterpriseId
            }
        }
        const response = await appapi.get(`/enterprise/${user.enterpriseId}/roles`, params);
        if (response.ok) {
            const data = response.data;
            if (data.status === 'success') {
                let roles = data?.data;
                dispatch(setRoles(roles))
            }
        }
    }

    /**
     * LoggedInEnterpriseUser Detials
     */
    const getEnterpriseUserDetails = async () => {
        dispatch(setLoading(true))
        let params = {
            params: {
                enterpriseId: user.enterpriseId,
                enterpriseUserId: user.enterpriseUserId
            }
        }
        let dataObj = {}
        let enterpriseResponse = await appapi.get(`/user/${user.enterpriseId}/${user.enterpriseUserId}`, params);
        if (enterpriseResponse.ok) {
            const enterpriseData = enterpriseResponse.data;
            if (enterpriseData.status === 'success') {
                dataObj = { ...enterpriseData.data }
                dispatch(login({ ...user, ...dataObj }))
                let find = routingList.find((item) => item?.moduleName === dataObj?.moduleName);
                if (find) {
                    navigate(`/${find.name}`)
                } else {
                    navigate(`/ecoInitiatives`)
                }
                dispatch(setLoading(false))
            } else {
                dispatch(setLoading(false))
            }
        } else {
            dispatch(setLoading(false))
        }
    }

    useEffect(() => {
        getcities();
        getcountries();
        getRoles();
        getEnterpriseUserDetails();
    }, []);

    /**
     * ClickHamburger
     * @param {*} event 
     * The Navigation Icon Expands the navigation on click
     */
    const clickHamburger = (event) => {
        event.stopPropagation()
        setExpanded(!expanded)
    }

    return (
        <div className='dashboardContainer'>
            <Stack direction='row'>
                {/**
                 * The Side Navigation Starts here
                 * @function clickHamburger is used here to expand or collapse the side menu
                 */}
                <div ref={boxRef} onClick={(event) => event.stopPropagation()} className={expanded ? 'expandsideNav' : 'shrinksideNav'} style={{ background: colors.primary, width: expanded ? '256px' : '56px', position: isMobile ? 'fixed' : 'relative', top: '0', left: '0' }}>
                    <Stack>
                        <div className='hamburgerwrapper' style={{ width: '56px', height: '56px', background: colors.primary }}>
                            <IconButton onClick={clickHamburger} style={{ width: '40px', height: '40px' }}>
                                <MenuIcon className="svgIconsMenu" />
                            </IconButton>
                        </div>
                        <Stack sx={{ height: isMobile ? 'calc(100vh - 56px)' : 'calc(100vh - 72px)', position: 'relative', display: isMobile && !expanded ? 'none' : 'initial' }}>
                            {sideMenuList?.map((menu, index) => (
                                menu.flag ?
                                    <NavLink onClick={() => isMobile && setExpanded(false)} key={index} className={({ isActive }) => (isActive ? 'activeMenu' : 'inActiveMenu') + ' flex-aligncenter linkStyle ' + (menu.name === 'Settings' ? 'settingStyle' : '')} style={{ textDecoration: 'none' }} to={`/${menu.route}`}>
                                        <Tooltip
                                            disableInteractive
                                            componentsProps={{
                                                tooltip: {
                                                    sx: {
                                                        fontSize: '12px',
                                                        fontFamily: 'Roboto',
                                                        fontWeight: '400',
                                                        bgcolor: colors.primaryDark,
                                                        '& .MuiTooltip-arrow': {
                                                            color: colors.primaryDark,
                                                            width: '0.5em !important',
                                                            height: '0.8em !important',
                                                            marginLeft: '-0.5em !important'
                                                        },
                                                    },
                                                },
                                            }}
                                            key={index} title={!expanded && menu.name} placement="right" arrow>
                                            <div className='menuStyle'>
                                                <img src={menu.icon} alt={menu.name} />
                                            </div>
                                        </Tooltip>
                                        {expanded ? <Typography className='menuName fs-12 fw-medium ff-Roboto expandedAnimation'> {menu.name} </Typography> : ''}
                                    </NavLink>
                                    :
                                    ''
                            ))}
                        </Stack>
                    </Stack>
                </div>

                {/* Main Content will be loaded here */}
                <div style={{ flex: '1', width: `calc(100%  - 256px)` }} >
                    <Header />
                    <Outlet />
                </div>
            </Stack>
        </div>
    )
}
